
import { computed, defineComponent, onMounted, reactive, Ref, ref, watch } from 'vue'

import { NLoadingBarProvider } from 'naive-ui'

import { ITable } from '@/helpers/interfaces'
import { Schema } from '@/components/Schema'
import { SelectModal } from '@/components/SelectModal'

interface ISelect {
  value:string
  key:string
}

export default defineComponent({
  name: 'APP',
  setup () {
    const selects = ref<ISelect[]>([])
    const modal = ref<boolean>(true)

    const tables = ref<ITable[]>([])
    const tablesNames = computed(() => tables.value.map(v => ({ label: v.classname, value: v.classname })))

    const mapTables = ref<Map<string, Ref<ITable>>>(new Map())

    const updateTables = (data: ISelect[]) => {
      mapTables.value.clear()
      data.map(d => {
        const item = tables.value.find(t => t.classname === d.value)
        if (item) {
          const data = {
            ...item,
            name: d.key,
            renderOptions: {
              position: { x: 0, y: 0 },
              size: { width: 0, height: 0 },
              expanded: true
            },
            filter: {
              columns: [...item.columns.map(s => s.field)],
              allColumns: true,
              methods: []
            }
          }
          mapTables.value.set(d.key, ref(data))
        }
      })
      state.selects = data
    }
    const state = reactive({
      selects,
      mapTables
    })

    const updateResponse = () => {
      (window as unknown as (Window & { Retool: any })).Retool.modelUpdate({ response: tables.value })
    }

    watch(tables.value, (val, oval) => {
      console.log({ val, oval })
      if (val !== oval) {
        updateResponse()
      }
    })

    onMounted(() => {
      (window as unknown as (Window & { Retool: any })).Retool.subscribe((model: any) => {
        tables.value = model.tables
      })
    })

    return () => (
      <NLoadingBarProvider loadingBarStyle={{ loading: { background: 'rgb(32 128 240)' } }}>
        <Schema tables={mapTables.value} selects={selects.value}/>
        <SelectModal show={modal} value={state.selects} onUpdateValue={updateTables} tables={tablesNames.value}/>
      </NLoadingBarProvider>
    )
  }
})

