import { defineComponent, onBeforeUnmount, onMounted, PropType, Ref, ref, toRefs, onBeforeUpdate } from 'vue'

import { ready } from '@jsplumb/browser-ui'
import { AnchorLocations } from "@jsplumb/common"

import { DataTableColumns, NCard, NTooltip, NButton, NIcon, NCheckboxGroup, NSpace, NCheckbox } from 'naive-ui'
import { DataBarVertical24Filled, DataBarVerticalAdd24Filled, Edit16Filled, Link16Filled } from "@vicons/fluent"

import { IColumn, ITable, ISelect } from '@/helpers/interfaces'
import { injectStrict } from '@/helpers/functions'
import { JsPlumbKey } from '@/helpers/symbols'

interface RelationRef {
  [x: string]: Element
}

const Table = defineComponent({
  name: 'TABLE',
  emits: ['openModal'],
  props: {
    table: {
      type: Object as PropType<Ref<ITable>>,
      required: true,
    },
    selects: {
      type: Array as PropType<ISelect[]>,
      required: true
    }
  },
  setup(props, { emit }) {

    const { table } = toRefs(props)

    const relations = [...table.value.relations.map((v) => v.local_key), "id"]
    const relationables = ref<RelationRef>({})
    const dom = ref()
    const jsplumb = injectStrict(JsPlumbKey)

    const conectRelations = () => {
      ready(() => {
        jsplumb.value.batch(() => {
          for (const relation of table.value.relations) {
            const model = props.selects.find(v => v.value == relation.related_model)
            if (model) {
              const sourceUuid = table.value.name + '.' + relation.local_key
              const targetUuid = model.key + '.' + relation.related_key
              jsplumb.value.connect({
                uuids: [sourceUuid, targetUuid]
              })
            }
          }

        })
      })
    }

    onMounted(() => {
      ready(() => {
        jsplumb.value.batch(() => {
          jsplumb.value.manage(dom.value.$el)

          for (const key in relationables.value) {
            const elementNode = relationables.value[key]
            const uuid = table.value.name + '.' + key
            const endpoint = jsplumb.value.addEndpoint(
              elementNode,
              {
                anchor: AnchorLocations.ContinuousLeftRight,
                target: true,
                source: true,
              },
              {
                uuid,
                maxConnections: -1,
                paintStyle: {
                  stroke: 'white',
                  strokeWidth: 2
                },
                connectorStyle: {
                  strokeWidth: 2,
                  outlineStroke: "white",
                  outlineWidth: 1
                },
                connectorOverlays: [
                  {
                    type: 'Label',
                    options: {
                      id: 'label',
                      cssClass: 'jtk-relationship-name'
                    }
                  }
                ]
              }
            )
            endpoint.setData({
              relations: table.value.relations.filter(v => v.local_key == key),
              name: table.value.name,
              classname: table.value.classname
            })
          }
        })
      })
    })

    onBeforeUpdate(() => {
      relationables.value = {}
    })

    onBeforeUnmount(() => {
      ready(() => {
        jsplumb.value.batch(() => {
          jsplumb.value.deleteConnectionsForElement(dom.value.$el)
          jsplumb.value.removeAllEndpoints(dom.value.$el, true)
        })
      })
    })

    const columns = [
      {
        title: "Columna",
        key: "field",
        render: (row) => (
          <span
            ref={(el) => {
              if (el && relations.includes(row.field)) {
                Object.assign(relationables.value, { [row.field]: el })
              }
            }}
          >
            {row.field}
            <span style="font-size: 10px;color: gray;text-transform: uppercase;">{row.type.split(' ')[0]}</span>
          </span>
        ),
      },
    ] as DataTableColumns<IColumn>

    return () => (
      <>
        <NCard
          ref={dom}
          class="table draggable"
          title={table.value.name}
          size="small"
          hoverable
          segmented={{
            footer: 'soft',
            content: 'soft'
          }}
          content-style="padding: 0; margin: 0;"
        >
          {{
            header: () => (
              <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column'}}>
                <span>{table.value.name}</span>
                <span style={{fontSize: '13px'}}>{table.value.classname}</span>
              </div>
            ),
            default: () => (
              <>
                {/* <NSwitch v-model={[true, 'value']} /> */}
                <NCheckboxGroup v-model={[table.value.filter.columns, 'value']}>
                  <NSpace vertical>
                    {table.value.columns.map((c, i) => (
                      <div
                        ref={(refa: any) => {
                          if (refa && relations.includes(c.field)) {
                            Object.assign(relationables.value, { [c.field]: refa })
                          }
                        }}
                        style={{ padding: '0 10px', width: '100%', display: 'flex' }}
                        key={i}
                      >
                        <NCheckbox value={c.field}>
                          <span>{c.field}</span>
                          <span style="font-size: 10px;color: gray;text-transform: uppercase;margin-left:10px;">{c.type.split(' ')[0]}</span>
                        </NCheckbox>
                      </div>
                    ))}
                  </NSpace>
                </NCheckboxGroup>
              </>
            ),
            footer: () => (
              <>
                <div>
                  {table.value.filter.methods.map((f,i) => (
                    <NSpace justify="space-between">
                      <p style="margin: 0px;gap: 10px;display: flex;align-items: center;"><NIcon><DataBarVertical24Filled /></NIcon>{f.name}</p>
                      <NButton text size="large" onClick={() => {
                        emit('openModal', table.value.name, i)
                      }}>
                        {{
                          icon: () => <NIcon><Edit16Filled /></NIcon>
                        }}
                      </NButton>
                    </NSpace>
                  ))}
                </div>
              </>
            ),
            action: () => (
              <>
                <NTooltip trigger="hover">
                  {{
                    default: () => 'Agregar Filtros',
                    trigger: () => (
                      <NButton text size="large" onClick={() => emit('openModal', table.value.name)}>
                        {{
                          icon: () => <NIcon><DataBarVerticalAdd24Filled /></NIcon>
                        }}
                      </NButton>
                    )
                  }}
                </NTooltip>
                <NTooltip trigger="hover">
                  {{
                    default: () => 'Autoconectar',
                    trigger: () => (
                      <NButton text size="large" onClick={conectRelations}>
                        {{
                          icon: () => <NIcon><Link16Filled /></NIcon>
                        }}
                      </NButton>
                    )
                  }}
                </NTooltip>
              </>
            )
          }}

        </NCard>
      </>
    )
  }
})

export {
  Table
}
