import { defineComponent, PropType, toRefs } from 'vue'

import { NCard, NModal, NInput, NDynamicInput, NForm, NFormItem, NSwitch, NRadioGroup, NSpace, NRadio, NButton } from 'naive-ui'
import { IMethod } from '@/helpers/interfaces'

const FilterModal = defineComponent({
  name: 'FilterModal',
  emits: ['closeModal'],
  props: {
    filter: {
      type: Object as PropType<IMethod>,
      required: true
    },
    show: {
      type: Boolean,
      required: true
    }
  },
  setup(props, { emit }) {

    const { show, filter } = toRefs(props)
    
    return () => (
      <NModal show={show.value}>
        <NCard
          title="Metodo"
          bordered={false}
        >
          <NForm
            model={filter.value}
          >
            <NFormItem label="Nombre" path="name">
              <NInput v-model={[filter.value.name, 'value']} placeholder="Nombre Metodo"/>
            </NFormItem>
            <NFormItem label="Personalizado" path="xtreme">
              <NSwitch v-model={[filter.value.xtreme, 'value']} />
            </NFormItem>
            {
              filter.value.xtreme &&
              <NFormItem label="Funcion" path="xtremeData">
                <NInput v-model={[filter.value.xtremeData, 'value']} placeholder="Funcion personalizada"/>
              </NFormItem>
            }
            <NFormItem label="Tipo de Parametro" path="xtremeType">
              <NRadioGroup v-model={[filter.value.paramType, 'value']} name="radiogroup1">
                <NSpace>
                  <NRadio value="pair">Objecto</NRadio>
                  <NRadio value="input">Array</NRadio>
                </NSpace>
              </NRadioGroup>
            </NFormItem>
            <NFormItem label="Parametros" path="params">
              <NDynamicInput
                v-model={[filter.value.params, 'value']}
                placeholder="Please type here"
                preset={filter.value.paramType}
                key-placeholder="Ingrese el nombre de la llave"
                value-placeholder="Ingrese el valor de la llave"
              />
            </NFormItem>
          </NForm>
          <NSpace justify="end" style="gap: 10px;">
            <NButton text size="large" onClick={() => emit('closeModal', true, filter.value)}> Guardar </NButton>
            <NButton text size="large" onClick={() => emit('closeModal', false)}> Cancelar </NButton>
          </NSpace>
        </NCard>
      </NModal>
    )
  }
})

export {
  FilterModal
}
