import { defineComponent, PropType, ref, reactive, toRefs, Ref} from 'vue'

import { NCard, NModal, NInput, NDynamicInput, NSelect, SelectOption, NFormItem, NForm, FormRules, NButton } from 'naive-ui'

import { ISelect } from '@/helpers/interfaces'

const SelectModal = defineComponent({
  emits: ['updateValue', 'click'],
  name: 'SELECTMODAL',
  props: {
    show: {
      type: Object as PropType<Ref<boolean>>,
      required: true
    },
    value: {
      type: Array as PropType<{ key: string, value: string }[]>,
      required: true
    },
    tables: {
      type: Array as PropType<SelectOption[]>,
      required: true
    }
  },
  setup(props, { emit }) {

    const { show } = toRefs(props)

    const state = reactive({
      data: ref<ISelect[]>(props.value.map(v => v))
    })

    const rule = {
      input: {
        required: true,
        validator (rule, value) {
          if (!value) {
            return new Error('Ingrese una clave')
          } else if (state.data.filter(v => v.key == value).length > 1) {
            return new Error('La clave esta en uso')
          }
          return true
        },
        trigger: ['input', 'blur']
      },
      select: {
        required: true,
        trigger: ['blur', 'change'],
        message: 'Seleccione un modelo'
      }
    } as FormRules

    const save = () => {
      emit('updateValue', state.data)
      show.value = false
    }

    const cancel = () => {
      show.value = false
    }

    return () => (
      <NModal show={show.value}>
        <NCard
          title="Metodo"
          bordered={false}
        >
          <>
            <NForm model={state}>
              <NDynamicInput
                preset='pair'
                value={state.data}
                onCreate={(v) => state.data.push({value: '', key: ''})}
                onRemove={(v) => console.log(v)}
                v-slots={{
                  default: ({value, index}: {value: {value:string, key:string}, index: number}) => (
                    <div style="width: 100%;">
                      <div style="display: flex; align-items: center;">
                        <NFormItem
                          rule={rule.input}
                          ignore-path-change
                          show-label={false}
                          path={`data[${index}].key`}
                        >
                          <NInput
                            internalDeactivateOnEnter={true}
                            v-model={[value.key, 'value']}
                            placeholder='Ingrese una clave'
                            style="margin-right: 12px; width: 260px;"
                            />
                        </NFormItem>
                        <NFormItem
                          rule={rule.select}
                          style='width: 100%;'
                          ignore-path-change
                          show-label={false}
                          path={`data[${index}].value`}
                        >
                          <NSelect
                            v-model={[value.value, 'value']}
                            onUpdateValue={v => value.key = v}
                            options={props.tables}
                            placeholder='Seleccione un Modelo'
                          />
                        </NFormItem>
                      </div>
                    </div>
                  )
                }}
              >
              </NDynamicInput>
            </NForm>
            <NButton onClick={save}>Guardar</NButton>
            <NButton onClick={cancel}>Cancelar</NButton>
          </>
        </NCard>
      </NModal>
    )
  }
})

export {
  SelectModal
}