import { defineComponent, onMounted, PropType, provide, reactive, Ref, ref, toRefs, watch } from 'vue'

import { BrowserJsPlumbInstance, ContainmentType, newInstance, ready } from '@jsplumb/browser-ui'
import { FullOverlaySpec, CustomOverlayOptions } from "@jsplumb/common"
import { ConnectionEstablishedParams, EVENT_CONNECTION, LabelOverlay } from '@jsplumb/core'

import { IMethod, IRelation, ITable, ISelect } from '@/helpers/interfaces'
import { JsPlumbKey } from '@/helpers/symbols'
import { FlowchartConnector, FlowchartConnectorOptions } from '@jsplumb/connector-flowchart'
import { Table } from './Table'
import { FilterModal } from './FilterModal'
import { NButton, NButtonGroup, NCard, NIcon, NModal, NSpace } from 'naive-ui'
import { EyeHide20Filled, EyeShow20Filled, FullScreenMaximize24Filled, FullScreenMinimize24Filled, ZoomIn20Filled, ZoomOut20Filled } from '@vicons/fluent'

const Schema = defineComponent({
  name: 'SCHEMA',
  props: {
    selects: {
      type: Array as PropType<ISelect[]>,
      required: true
    },
    tables: {
      type: Object as PropType<Map<string, Ref<ITable>>>,
      required: true
    }
  },
  setup(props) {
    const { tables, selects } = toRefs(props)

    const jsplumb = ref<BrowserJsPlumbInstance>()
    const board = ref<HTMLElement>()
    const scrollable = ref<HTMLElement>()
    const fullscreen = ref<boolean>(false)
    const modalFilter = ref(false)
    const modalResult = ref<boolean>(false)
    const filter = ref<IMethod>({} as IMethod)
    const table = ref('')
    const inx = ref(-1)
    const zoom = ref(1)
    const state = reactive({
      filter,
      table,
      inx
    })
    const position = ref({
      x: 0,
      y: 0,
      top: 0,
      left: 0,
    })

    onMounted(() => {
      ready(() => {
        const container = document.getElementById("board")
        const instance = newInstance({
          dragOptions: {
            cursor: 'pointer',
            trackScroll: true,
            zIndex: 2000,
            grid: { w: 10, h: 10 },
            containment: ContainmentType.notNegative,
          },
          maxConnections: -1,
          connector: {
            type: FlowchartConnector.type,
            options: {
              stub: [10, 50],
              gap: 10,
              cornerRadius: 5,
              alwaysRespectStubs: true,
              cssClass: 'jtk-conector-up-always'
            } as FlowchartConnectorOptions,
          },
          container: container!
        })

        instance.batch(() => {
          instance.bind(EVENT_CONNECTION, function (connInfo: ConnectionEstablishedParams) {
            const { connection, sourceEndpoint, targetEndpoint } = connInfo
            const sourceEndData = (sourceEndpoint.getData() as { relations: IRelation[], name: string, classname: string })
            const targetEndData = (targetEndpoint.getData() as { relations: IRelation[], name: string, classname: string })

            const trueTargetData = sourceEndData.relations.find(r => r.related_model == targetEndData.classname)

            const label = connection.getOverlay<LabelOverlay>("label")
            label.setLabel(trueTargetData?.type || 'UNDEFINED')
            label.component.addClass((trueTargetData?.type || 'UNDEFINED'))
            connection.addOverlay({
              type: 'Custom',
              options: {
                location: [5, 0],
                id: 'symbol',
                create: () => {
                  let d = document.createElement('div')
                  d.onclick = (e: Event) => {
                    jsplumb.value?.deleteConnection(connection)
                    e.preventDefault()
                    e.stopPropagation()
                  }
                  d.className = 'jtk-relationship-symbol ' + trueTargetData?.type || 'UNDEFINED'
                  d.style.cursor= 'pointer'
                  let label = ''
                  switch (trueTargetData?.type) {
                    case 'BelongsTo':
                      label = '-1:1>'
                      break
                    default:
                      label = '-?>'
                      break
                  }
                  d.innerHTML = '<span>' + label + '</span>'
                  return d
                }
              } as CustomOverlayOptions
            } as FullOverlaySpec)

          })
        })

        jsplumb.value = instance
      })
    })

    provide(JsPlumbKey, jsplumb)

    const openModal = (key: string, keyFilter?: number) => {
      state.table = key
      const table = tables.value.get(key)
      if (!table) return
      if (keyFilter !== undefined) {
        state.inx = keyFilter
        state.filter = Object.assign({}, table.value.filter.methods[keyFilter])
      } else {
        state.filter = { name: '', xtreme: false, xtremeData: '', paramType: 'input', params: [] }
        state.inx = table.value.filter.methods.length 
      }
      modalFilter.value = true
    }

    const closeModal = (save: boolean, rfilter: IMethod) => {
      if (save) {
        const ctable = tables.value.get(state.table)!
        ctable.value.filter.methods[state.inx] = rfilter
      }
      modalFilter.value = false
    }

    const handleFullScreen = (value: boolean) => {
      if (value) {
        document.documentElement.requestFullscreen()
      } else {
        document.exitFullscreen()
      }
      fullscreen.value = value
    }

    const moveHandler = (e: MouseEvent) => {
      if (scrollable.value) {
        const dx = e.clientX - position.value.x
        const dy = e.clientY - position.value.y

        
        if (zoom.value < 1) {
          const toDecimal = (num: number) => Math.round((num + Number.EPSILON) * 100) / 100
          let zoomF = toDecimal(zoom.value)
          let stop = scrollable.value.scrollTop
          let sheight = scrollable.value.scrollHeight
          let cheight = scrollable.value.clientHeight
          let zoomOF = 1 / zoomF

          if (stop < (sheight - (cheight * zoomOF)) / zoomOF) {
            scrollable.value.scrollTop = position.value.top - dy
          } else if ((position.value.top - dy) < scrollable.value.scrollTop){
            scrollable.value.scrollTop = position.value.top - dy
          }
        } else {
          scrollable.value.scrollTop = position.value.top - dy
        }
        scrollable.value.scrollLeft = position.value.left - dx
      }
    }
    const upHandler = () => {
      if (scrollable.value) {
        document.removeEventListener('mousemove', moveHandler)
        document.removeEventListener('mouseup', upHandler)
      }
    }

    const downHandler = (e: MouseEvent) => {
      if (scrollable.value) {
        position.value = {
          left: scrollable.value.scrollLeft,
          top: scrollable.value.scrollTop,
          x: e.clientX,
          y: e.clientY,
        }

        document.addEventListener('mousemove', moveHandler)
        document.addEventListener('mouseup', upHandler)
      }
    }

    const handleZoom = (step: number) => {
      let newValue = step + zoom.value
      if (board.value && (newValue >= 0.5 && newValue <= 1.6)) {
        board.value.style.transform = `scale(${newValue})`
        jsplumb.value?.setZoom(newValue)
        zoom.value = newValue
      }
    }

    return () => (
      <>
        <div
          ref={scrollable}
          class="scrollable"
          onMousedown={downHandler}
        >
          <div id="board" ref={board}>
            {
              jsplumb.value && (() => {
                let data = []
                for (const [key, value] of tables.value) {
                  data.push(<Table key={key} table={value} selects={selects.value} onOpenModal={openModal} />)
                }
                return data
              })()
            }
          </div>
        </div>
        <NSpace style="width: 100%; padding: 10px; position: fixed; bottom: 0px;" justify="space-between">
          <NButton type="info" size="small" round onClick={() => modalResult.value = !modalResult.value}>
            {{
              icon: () => (<NIcon>{ modalResult ? <EyeShow20Filled /> : <EyeHide20Filled/>}</NIcon>)
            }}
          </NButton>

          <NButtonGroup size="small">
            <NButton type="info" onClick={() => handleZoom(-.1)} round>
              {{
                icon: () => (<NIcon><ZoomOut20Filled /></NIcon>)
              }}
            </NButton>
            <span style={{
              borderLeft: '1px solid white',
              borderRight: '1px solid white',
              background: '#2080f0',
              color: 'white',
              display: 'flex',
              alignItems: 'center',
              padding: '0px 10px'
            }}>{zoom.value.toFixed(2)}</span>
            <NButton type="info" onClick={() => handleZoom(+.1)} round >
              {{
                icon: () => (<NIcon><ZoomIn20Filled /></NIcon>)
              }}
            </NButton>
          </NButtonGroup>
          <NButton type="info" size="small" onClick={() => handleFullScreen(!fullscreen.value) } round>
            {{
              icon: () => <NIcon>{!fullscreen.value ? <FullScreenMaximize24Filled /> : <FullScreenMinimize24Filled />}</NIcon>
            }}
          </NButton>
        </NSpace>
        <FilterModal show={modalFilter.value} onCloseModal={closeModal} filter={state.filter}/>
        <NModal v-model={[modalResult.value, 'show']}>
          <NCard
            title="Metodo"
            bordered={false}
          >
            <pre>{JSON.stringify(tables.value, null, 2)}</pre>
          </NCard>
        </NModal>
      </>
    )
  },
})

export {
  Schema
}